import React from 'react';
import Button from '../button/button';
import styles from './card_edit_form.module.css';

const CardEditForm = ({ FileInput, order, updateOrder, deleteOrder }) => {
  const {
    title,
    status,
    sender,
    sender_tel,
    send_date,
    receiver,
    receiver_tel,
    receive_date,
    message,
    fileName,
    editing,
  } = order;

  const onFileChange = file => {
    updateOrder({ ...order, fileName: file.name, fileURL: file.url });
  };
  const onChange = e => {
    if (e.currentTarget === null) return;
    e.preventDefault();
    updateOrder({
      ...order,
      [e.currentTarget.name]: e.currentTarget.value,
    });
  };
  const onClick = () => {
    deleteOrder(order);
  };

  return (
    <form className={styles.form}>
      <div className={styles.row}>
        <input
          className={styles.title}
          type="text"
          name="title"
          value={title}
          onChange={onChange}
        />
        <select
          defaultValue={status}
          className={styles.select}
          name="status"
          onChange={onChange}
        >
          <option value="receipt"></option>
          <option value="working"></option>
          <option value="complete"></option>
          <option value="hold"></option>
        </select>
      </div>
      <div className={styles.row}>
        <div className={styles.cell}>
          <label className={styles.label} htmlFor="sender">
            
          </label>
          <input
            className={styles.input}
            type="text"
            id="sender"
            name="sender"
            value={sender}
            onChange={onChange}
          />
        </div>
        <div className={styles.cell}>
          <label className={styles.label} htmlFor="sender_tel">
            
          </label>
          <input
            className={styles.input}
            type="tel"
            id="sender_tel"
            name="sender_tel"
            value={sender_tel}
            onChange={onChange}
          />
        </div>
        <div className={styles.cell}>
          <label className={styles.label} htmlFor="send_date">
            
          </label>
          <input
            className={styles.input}
            type="date"
            name="send_date"
            value={send_date}
            onChange={onChange}
          />
        </div>
      </div>
      <div className={styles.row}>
        <div className={styles.cell}>
          <label htmlFor="receiver" className={styles.label}>
            
          </label>
          <input
            className={styles.input}
            type="text"
            id="receiver"
            name="receiver"
            value={receiver}
            onChange={onChange}
          />
        </div>
        <div className={styles.cell}>
          <label htmlFor="receiver_tel" className={styles.label}>
            
          </label>
          <input
            className={styles.input}
            type="tel"
            id="receiver_tel"
            name="receiver_tel"
            value={receiver_tel}
            onChange={onChange}
          />
        </div>
        <div className={styles.cell}>
          <label htmlFor="receive_date" className={styles.label}>
            
          </label>
          <input
            className={styles.input}
            type="date"
            id="receive_date"
            name="receive_date"
            value={receive_date}
            onChange={onChange}
          />
        </div>
      </div>
      <div className={styles.row}>
        <textarea
          className={styles.textarea}
          name="message"
          value={message}
          onChange={onChange}
        />
      </div>
      <div className={styles.row}>
        <div className={styles.fileInput}>
          <FileInput fileName={fileName} onFileChange={onFileChange} />
        </div>
        {editing && <Button name="Delete" onClick={onClick} />}
      </div>
    </form>
  );
};

export default CardEditForm;
